* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

html {
    font-family: 'Poppins', sans-serif;
    font-style: normal;
    font-weight: 400;
    background-color: #fafbfc;
    color: #05004e;
}

h1,
.h1 {
    font-size: 32px;
    font-weight: 600;
}

h2,
.h2 {
    font-size: 26px;
    font-weight: 500;
}

h3,
.h3 {
    font-size: 22px;
    font-weight: 500;
}

h4,
.h4 {
    font-size: 18px;
    font-weight: 400;
}

h5,
.h5 {
    font-size: 16px;
    font-weight: 400;
}

p {
    font-size: 12px;
    color: #737791;
}
